@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap);
body{margin:0;font-family:"Raleway", serif !important;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

*,html{padding:0;margin:0}.icon{width:50px;height:50px;margin:1em 0em 1em 1em}.App{background:#057DCD}

.about-container{margin-top:3.75em;background:#1E3D58;color:#E8EEF1;height:600px;display:flex}.about-info-container{flex:1 1;padding:1.25em}.about-picture{flex:1 1;padding-left:5em}.pic{width:550px;height:550px}.links-info{padding-top:2.5em;text-align:center}.about-icon{height:43px;width:43px;margin-left:2em}

.skills-container{background:#057DCD;height:300px;text-align:center;display:flex;flex-direction:column;justify-content:center}.skills-items{display:flex;justify-content:center}.icon{height:75px;width:75px;padding:5px;background:#E8EEF1;border-radius:10px;border:2px solid #1E3D58}

.projects-container{height:600px}.projects-container h1{text-align:center}.card-container{display:flex}.cards{margin:2em 0 2em 2em;background:#E8EEF1 !important;border-radius:10px !important;border:2px solid #1E3D58 !important}.btn-link{background:#1E3D58 !important;border-color:#1E3D58 !important;color:#E8EEF1 !important;text-decoration:none !important}.btn-link:hover{background:#28517a !important}

.contact-container{height:300px;text-align:center}

.nav{background:#1E3D58;font-size:1.2rem}

.footer-container{background:#232323;color:white;display:flex;align-items:center;justify-content:center;height:60px}.footer-creator h5{text-align:center;margin-bottom:0}

